import React from 'react';
import Helmet from 'react-helmet';
import { StaticQuery, graphql } from 'gatsby';
import SchemaOrg from './SchemaOrg';
import PropTypes from 'prop-types';

const SEO = ({ meta, isProject }) => (
  <StaticQuery
    query={graphql`
      {
        site {
          siteMetadata {
            title
            description
            canonicalUrl
            image
          }
        }
      }
    `}
    render={({ site: { siteMetadata: seo } }) => {
      const pageMeta = meta || {};

      const title = pageMeta.title ? `${seo.title} | ${pageMeta.title}` : seo.title;
      const description = pageMeta.description || seo.description;
      const image = pageMeta.image ? `${seo.canonicalUrl}${pageMeta.image}` : seo.image;
      const url = pageMeta.slug ? `${seo.canonicalUrl}/${pageMeta.slug}` : seo.canonicalUrl;
      const datePublished = isProject ? pageMeta.datePublished : false;

      return (
        <React.Fragment>
          <Helmet>
            {/* General tags */}
            <title>{title}</title>
            <meta name="description" content={description} />
            <meta name="image" content={image} />

            {/* OpenGraph tags */}
            <meta property="og:url" content={url} />
            {isProject ? <meta property="og:type" content="article" /> : null}
            <meta property="og:title" content={title} />
            <meta property="og:description" content={description} />
            <meta property="og:image" content={image} />
          </Helmet>
          <SchemaOrg
            isCreativeWork={isProject}
            url={url}
            title={title}
            image={image}
            description={description}
            datePublished={datePublished}
            canonicalUrl={seo.canonicalUrl}
            defaultTitle={seo.defaultTitle}
          />
        </React.Fragment>
      );
    }}
  />
);

SEO.propTypes = {
  isProject: PropTypes.bool,
  meta: PropTypes.any
};

SEO.defaultProps = {
  isProject: false,
  meta: {},
};

export default SEO